import React from 'react';
import "./index.scss";
import AuthorImg from "../../assets/img/author/author-bg-2.jpg";

const Author = () => {
  return (
    <div className='author-container container' id='author'>
      <div className='box'>
        {/* <div className='bg'></div> */}
        <img className='author-img' src={AuthorImg} alt="Author-Img" />
        <div className='title'>
          <h2> </h2>
          <div className='title-text'>
            <h3> </h3>
            <ul className='title-list'>
              <li className='title-list-item'>
                <div className="title-svg"></div>
                <span>17             ,     </span>
              </li>
              <li className='title-list-item'>
                <div className="title-svg"></div>
                <span>    </span>
              </li>
              <li className='title-list-item'>
                <div className="title-svg"></div>
                <span>     </span>
              </li>
              <li className='title-list-item'>
                <div className="title-svg"></div>
                <span>      :</span>
              </li>
                <ul className='title-sublist'>
                  <li className='title-sublist-item'>-  English Tenses: Weekend with Eddie</li>
                  <li className='title-sublist-item'>-  General English</li>
                  <li className='title-sublist-item'>- A-to-Z of Business and Economics: mini-dictionary</li>
                  <li className='title-sublist-item'>- English planner</li>
                  <li className='title-sublist-item'>-        </li>
                </ul>
              <li className='title-list-item'>
                <div className="title-svg"></div>
                <span>    , ,    -</span>
              </li>
              <li className='title-list-item'>
                <div className="title-svg"></div>
                <span>        :       </span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Author;